@use '../mixins' as *; // Make mixins available
#marketPlaceCards {
  label {
    font-size: 4em;
    cursor: pointer;
  }

  .container-fluid {
    .col-sm-3 {
      border-top: 5px solid white;
      border-left: 5px solid white;

      &:nth-child(2) {
        border-right: 5px solid white;
      }

      &:nth-child(4) {
        border-right: 5px solid white;
      }

      &:hover {
        @include selected-state(#555ce1);
      }

      &.selected {
        @include selected-state(#555ce1);
      }

      label {
        margin: -10px auto 0 auto;
        width: 100%;
        height: 100%;
        cursor: pointer;

        .cards {
          input[type="radio"] {
            width: 30px;
            height: 30px;
            cursor: pointer;
          }

          img {
            height: 150px;
            margin: 0 auto;
          }

          .price {
            width: 60px;
            float: right;
            font-size: .5em;
            text-align: right;

            .price-icon {
              background: url('../../../images/market/coin_25x25.png') no-repeat;
              background-size: 25px;
              width: 25px;
              height: 25px;
              display: inline-block;
            }
          }
        }
      }
    }

    &:last-child {
      .col-sm-3 {
        border-bottom: 5px solid white;
      }
    }
  }
}